<script>
  // Component level mixins
  import Computed from './mixins/image-computed';
  import Methods from './mixins/image-methods';
  import Events from './mixins/image-events';

  export default {
    name: 'r-image',

    mixins: [
      Computed,
      Methods,
      Events
    ],

    props: {
      src: {
        type: String,
        required: true
      }
    },

    render (h) {
      const data = {
        staticClass: 'image',
        attrs: {
          src: this.src,
          ...this.$attrs
        }
      };
      return h('img', data);
    }
  };
</script>

<style lang="scss">
  @import "../../styles/components/_image.scss";
</style>



