/**
 * Image methods
 *
 * @mixin
 *
 * Methods for the r-image component
 */
import { ensureNumber } from '../../../util/helpers';

export default {
  methods: {
    ensureSrcSetItem (option, index) {
      if (typeof option === 'object') {
        return option;
      } else if (
        typeof option === 'string' || typeof option === 'number'
      ) {
        const size = this.getFormattedSize(option);
        const item = {
          size: `${index + 2}x`,
          src: option
        };
        if (size) {
          item.size = size;
          item.src = this.getSizedImage(size);
        }
      }
    },

    getFormattedSize (size) {
      if (typeof size === ensureNumber(size)) {
        return `${size}x`;
      }
      const unitsRegex = /^\d+[wx]$/i;
      return unitsRegex.test(size)
        ? size : null;
    },

    getSizedImage (size) {
      const defaultSuffix = '@';
      const extensionRegex = /\.(\w+)$/i;
      const nameRegex = /\/([a-zA-Z--0-9_. -]+)\.\w+$/i;
      const fullNameRegex = /\/([a-zA-Z--0-9_. -]+\.\w+)$/i;

      // checking for a proper image name
      if (!fullNameRegex.test(this.src)) {
        return this.src;
      }

      const extension = this.src.match(extensionRegex)[1];
      const name = this.src.match(nameRegex)[1];
      const pathTo = this.src.replace(fullNameRegex, '');

      return `${pathTo}${name}${defaultSuffix}${size}.${extension}`;
    }
  }
};



