<script>
  // Directives
  import Scroll from '../../directives/scroll';

  // Icons
  import MdClose from 'md-svg-vue/dist/navigation/MdClose.vue';

  // Components
  import RBtn from '../RBtn/RBtn.vue';
  import RCard from '../RCard/RCard.vue';
  import RCardText from '../RCard/RCardText.vue';
  import RCardActions from '../RCard/RCardActions.vue';
  import RToolbar from '../RToolbar/RToolbar.vue';
  import RToolbarTitle from '../RToolbar/RToolbarTitle.vue';

  let dialogCardNumber = 1;

  export default {
    name: 'r-dialog-card',

    components: {
      MdClose,
      RBtn,
      RCard,
      RCardText,
      RCardActions,
      RToolbar,
      RToolbarTitle
    },

    directives: {
      Scroll
    },

    props: {
      toolbarLight: Boolean,
      toolbarShadow: Boolean,
      toolbarColor: {
        type: String,
        default: 'white'
      },
      actionsStyles: Object,
      actionsClass: String
    },

    data () {
      return {
        id: dialogCardNumber++,
        toolbarFlat: true
      };
    },

    methods: {
      onContentScroll (ev) {
        if (this.toolbarShadow) {
          this.toolbarFlat = !(ev.target && ev.target.scrollTop > 0);
        }
      },
      closeDialog () {
        this.$emit('close');
      }
    },

    computed: {
      classes () {
        return {
          'dialog-card': true
        };
      },
      toolbarClasses () {
        return {
          'dialog-card__title': true
        };
      },
      actionsClasses () {
        return {
          'dialog-card__actions': true,
          [this.actionsClass]: !!this.actionsClass
        };
      }
    }
  };
</script>

<template>
  <r-card :class="classes" :id="'dialog-card-' + id">
    <template v-if="$slots.header">
      <slot name="header"></slot>
    </template>
    <template v-else>
      <r-toolbar v-if="$slots.title || $slots.close"
                 :class="toolbarClasses"
                 :color="toolbarColor"
                 :light="toolbarLight"
                 :flat="toolbarFlat">
        <r-toolbar-title>
          <slot name="title"></slot>
        </r-toolbar-title>
        <div class="dialog-card__close">
          <r-btn v-if="!$slots.close"
                 ripple icon flat round
                 :large="$rabotify.breakpoint.mdAndDown"
                 color="secondary" @click.native.stop="closeDialog()">
            <md-close></md-close>
          </r-btn>
          <slot v-else name="close"></slot>
        </div>
      </r-toolbar>
    </template>
    <r-card-text ref="text" class="dialog-card__inner"
                 v-scroll="{ callback: this.onContentScroll, target: '#dialog-card-' + this.id + ' .dialog-card__inner' }">
      <slot name="default"></slot>
    </r-card-text>
    <r-card-actions ref="actions"
                    v-if="$slots.actions"
                    :class="actionsClasses"
                    :style="actionsStyles">
      <slot name="actions"></slot>
    </r-card-actions>
  </r-card>
</template>

<style lang="scss">
  @import "../../styles/components/_dialog-card.scss";
</style>



