<script>
  import { normalizeClassName } from '../../util/helpers';

  export default {
    name: 'r-divider',

    functional: true,

    props: {
      inset: Boolean
    },

    render (h, { props, data, children }) {
      data.staticClass = normalizeClassName(`divider ${data.staticClass || ''}`);

      if (props.inset) {
        data.staticClass += ' divider_inset';
      }

      return h('hr', data);
    }
  };
</script>

<style lang="scss">
  @import "../../styles/components/_divider.scss";
</style>



